// keyframes

@-webkit-keyframes animation-top-bottom {
	0% {
		opacity: 0;
		top: -30px;
	}
	100% {
		opacity: 1;
		top: 0;
	}
}

@keyframes animation-top-bottom {
	0% {
		opacity: 0;
		top: -30px;
	} 
	100% {
		opacity: 1;
		top: 0;
	}
}
// end keyframes

// scrollbar
.mega-tab-content-list::-webkit-scrollbar-track {
	box-shadow: inset 0 0 4px var(--miscellaneous-color);
	-webkit-box-shadow: inset 0 0 var(--miscellaneous-color);
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.mega-tab-content-list::-webkit-scrollbar {
	width: 4px;
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.mega-tab-content-list::-webkit-scrollbar-thumb {
	background-color: var(--primary-color);
	border-radius: 5px;
}
.search-result-list::-webkit-scrollbar-track {
	box-shadow: inset 0 0 4px var(--miscellaneous-color);
	-webkit-box-shadow: inset 0 0 var(--miscellaneous-color);
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.search-result-list::-webkit-scrollbar {
	width: 4px;
	background-color: var(--miscellaneous-color);
	border-radius: 5px;
}
.search-result-list::-webkit-scrollbar-thumb {
	background-color: var(--primary-color);
	border-radius: 5px;
}
// end scrollbar
.section-announcement-bar {
	p {
		margin: 0;
	}
	@media (min-width: 1000px) {
		position: relative;
		z-index: 2;
	}
}
.header-section {
	position: relative;
	z-index: 6;
	&.header-sticky {
		position: sticky; 
		top: 0;
		left: 0;
		right: 0;
		z-index: layer(header-sticky);
		animation: animation-top-bottom .5s both;
		box-shadow: 0 2px 10px rgba(208, 208, 208, 0.4);  
		&[data-sticky="up"] {	
			animation: none;	
			&.is-scrolling-down {
				transform: translateY(-100%);
			}
			&.is-scrolling-up {
				transition: transform .2s ease-out;
			}
		}
		.vertical-menu-link {
			&:before {
				top: 85px;
				height: 100vh;
			}
		}
	}
	&:not(.header-sticky) {
		.header-overlay {
			.header-navigation {
				.main-navigation >.list-item {
					mega-dropdown >a {
						color: var(--header-absolute-text-color);
					}
				}
			}
			.vertical-menu-link {
				span {
					color: var(--header-absolute-text-color); 
				}
				svg {
					path {
						stroke: var(--header-absolute-text-color);
					}
				}
			}
			.header-information {
				color: var(--header-absolute-text-color);
			}
			.header-icons li svg path {
				stroke: var(--header-absolute-text-color);
				fill: none;
			}
			.header-icons .header-icon--cart svg path {
				stroke: unset;
				fill: var(--header-absolute-text-color);
			}
			.search-dropdown .btn-search svg path {
				stroke: var(--primary-color);
			}
			.header-multi-shop .multi-shop-list>li {
				color: var(--header-absolute-text-color);
				path {
					fill: var(--header-absolute-text-color);		
				}
			}
		}
	}
}
#shopify-section-header {
	position: relative;
	z-index: 1;
}
.popup-show {
	.header-sticky {
		.section-header {
			z-index: -1;
		}
	}
}
.header-sticky {
	.vertical-menu-link {
		&:before {
			top: 62px;
		}
	}
	.header-navigation .main-navigation>.has-dropdown:before {
		top: 62px;
	}
	.header-information {
		display: none;
	}
	@media (min-width: 1000px) {
		.header-multi-shop {
			display: none;
		}
	}
	.header-1 {
		@media (min-width: 1000px) {			
			.header-container>.flex {
				padding: 0;
				>div {
					padding-bottom: 0;
				}
				.header-logo {
					padding: 10px 0;
				}
			}
		}
		.header-form-search {
			display: none;
		}
		@media (min-width: 1240px) {
			.header-group {
				order: unset;
				width: auto;
				flex: 1;
			}
		}
		@media (min-width: 1000px) and (max-width: 1239px) {
			.header-icons {
				flex: 1;
			}
		}
	}
	.section-header.header-overlay {
		background: var(--header-background-color);
	}
	.hide-icon-list {
		@media (min-width: 1000px) {
			.header-icons {
				display: none;
			} 
		}
		.header-group {
			flex: 1;
		}
		@media (max-width: 1040px) {
			.header-navigation .main-navigation>li {
				padding-left: 11px;
				padding-right: 11px;
			}
		}
		.header-2 {
			.header-row {
				padding: 0;
			}
			.header-vertical-menu {
				flex: unset;
			}
			.header-information {
				display: none;
			}
		}
	}
}
body:not(.templateIndex) {
	.section-header {
		border-bottom: 1px solid var(--miscellaneous-color);
	}
}
body:not(.templateIndex) {
	.section-header.header-overlay {
		position: relative;
	}
}
.section-header {
	background: var(--header-background-color);
	&.header-overlay {
		background: var(--header-absolute-background-color);
	}
	.header-logo {
		flex: 1;
		max-width: 100%;
		@include margin-end(17px);
		@media (min-width: 1000px) {
			flex: 0 0 180px;
		}
		a {
			display: block;
			svg {
				display: flex;
			}
		}
		img {
			@include ab(0,0,n,n);
		}
	}
	.header-container {
		>.flex {
			width: 100%;
			align-items: center;
			justify-content: space-between;
			position: static;
		}
	}
	.header-group {
		>.flex {
			width: 100%;
			align-items: center;
		}
	}
}
.bubble-cart:before{
	content: attr(data-cart-count);
}
.header-information {
	color: var(--header-text-color);
	font-weight: var(--header-text-fw);
	a {
		color: var(--primary-color);
		text-decoration: underline;
		text-underline-offset: 1px;
	}
}
.header-icon-item {
	position: relative;
	@extend %transition;
	.bubble-count {
		background: var(--primary-color);
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		@extend %radius-circle;
	}
}
.header-icons {
	display: flex;
	.bubble-count {
		@include ab(-11px,n,n,-13px);
		width: 20px;
		height: 20px;
		font-size: 9px;
	}
	.header-icons-list {
		@include flex-end;
		width: 100%;
		align-items: center;
		a {
			display: flex;
			>div {
				display: flex;
			}
		}
	}
	li {
		@media (min-width: 1000px) {
			>* {
				@include margin-start(30px);
			}
		}
		svg {
			width: 20px;
			height: 20px;
			path {
				stroke: var(--header-text-color);
			}
		}
		&:hover {
			summary, >a, >div {
				svg {
					path {
						stroke: var(--primary-color);
					}
				}
			}
		}
	}
	.header-icon--cart {
		svg path {
			stroke: unset;
			fill: var(--header-text-color);
		}
		&:hover {
			svg path {
				stroke: unset;
				fill: var(--primary-color);
			}
		}
	}
	.header-icon--search {
		position: static;
		.icon-close {
			display: none;
		}
	}
	.mini-search {
		.header-search {
			display: flex;
		}
		&[open] {
			.header-search {
				.icon-close {
					display: block;
				}
				.icon-search {
					display: none;
				}
			}
		}
		@media (max-width: 749px) {
			padding: 5px;
			margin: -5px;
			svg {
				width: 16px;
				height: 16px;
				path {
					stroke: var(--primary-color);
				}
			}
		}
	}
}
.header-form-search {
	flex: 1;
	max-width: 100%;
	@include margin-end(17px);
	.form-container {
		max-width: 515px;
		width: 100%;
	}
	form {
		display: flex;
		background: var(--header-form-bg-color);
		@extend %radius-5px;
		&:focus {
			outline: 1px solid var(--primary-color);
		}
		input {
			padding: 5px 15px; 
			@include padding-start(23px);
			border: 0;
		}
	}	
	button {
		display: flex;
		align-items: center;
		svg {
			width: 16px;
			height: 16px;
		}
		path {
			stroke: var(--primary-color);
		}
	}
}
.btn-search {
	.icon-loading {
		display: none; 
	}
}
.is-searching {
	.btn-search {
		.icon-loading {
			display: block;
		}
		.icon-search {
			display: none;
		}
	}
}
.header-multi-shop {
	.multi-shop-list >li {
		cursor: pointer;
		font-size: 12px;
		user-select: none;
		color: var(--header-text-color);
		path {
			fill: var(--header-text-color);
		}
		@media (min-width: 1000px) {
			margin: 0 12.5px;
			details {
				min-width: 40px;
				padding: 5px 0;
			}
		}
		@media (min-width: 1000px) {			
			&:hover {
				color: var(--primary-color);
				svg {
					path {
						fill: var(--primary-color);
					}
				}
			}
		}
	}
	.multi-shop--switcher summary {
		span {
			@include padding-end(10px);
		}
	}
	.pa {
		@include text-left;
		top: 100%;
		@include left(6px);
		background: var(--background-color);
		min-width: 62px;
		padding: 10px 7px;
		box-shadow: 0px 1px 5px -2px rgba(0, 0, 0, 0.2);
		z-index: 2;
		@extend %radius-5px;
		@media (min-width: 1000px) {
			@include left(-7px);
		}
		li {
			padding: 5px 0;
			button {
				display: flex;
				align-items: center;
				font-size: 12px;
				line-height: 1.4em;
				white-space: nowrap;
				color: var(--text-color);
			}
			.currency-flag {
				margin-top: -1px;
				@include margin-end(5px);	
			}
			&.active button {
				color: var(--primary-color);
			}
		}
	}
}
.header-1 {
	.header-container {
		>.flex {
			padding: 10px 0;
		}
	}
	.header-multi-shop {
		@media (min-width: 1300px) {
			@include padding-end(4%);
		}
	}
	.header-group {
		order: 4;
		width: 100%;
	}
	@media (min-width: 1000px) {
		.header-container {
			>.flex {
				padding: 25px 0 0;
				>div {
					padding-bottom: 10px;
				}
				.header-group {
					padding-bottom: 0;
				}
			}
		}
	}
}
.header-2 {
	.header-container {
		>.flex {
			padding: 10px 0 0;
			>div {
				padding-bottom: 5px;
			}
		}
	}
	.header-vertical-menu {
		@include margin-end(37px);
	}
	.header-multi-shop {
		@include margin-start(20px);
	}
	.header-information {
		display: none;
		@media (min-width: 1600px) {
			display: block;
		}
	}
	.header-group {
		flex: 1;
	}
	&.layout-wide {
		@media (min-width: 1000px) and (max-width: 1400px) {
			.header-group {
				order: 5;
				flex: 0 0 100%;
				max-width: 100%;
			}
			.header-vertical-menu {
				flex: auto;
			}
		}
	}
	&.layout-boxed {
		.header-group {
			order: 5;
			flex: 0 0 100%;
			max-width: 100%;
		}
		.header-vertical-menu {
			flex: auto;
		}
	}
}
// navigation
.body:not(.templateIndex) {
	.dropdown {
		border-top: 1px solid var(--miscellaneous-color);
	}
}
.dropdown {
	@include ab(100%,auto,n,n);
	min-width: 200px;
	padding: 35px 0;
	will-change: transform;
	z-index: 11;
	background: var(--header-dropdown-bg-color);
	opacity: 0;
	visibility: hidden;
	transition-delay: 0.2s;
	@extend %transition;
}
.header-navigation {
	flex: 1;
	.main-navigation {
		display: flex;
		>.has-dropdown {
			.dropdown-link-parent {
				a:not(.btn) {
					color: var(--header-dropdown-text-color);
				}
			}
			>section {
				&:before {
					content: "";
					@include ab(100%,0,0,0);
					background: var(--header-overlay-color);
					pointer-events: none;
					height: 100vh;
				}
			}
			>div {
				border: 1px solid var(--miscellaneous-color);
				margin-top: -1px;
				.has-dropdown {
					>div {
						border: 1px solid var(--miscellaneous-color);
					}
				}
			}
			.dropdown {
				a:not(.btn) {
					font-weight: var(--header-dropdown-text-fw);
					&:hover {
						color: var(--primary-color);
					}
				}
			}
		}
		>.is-editing {
			&:before {
				opacity: 1;
				visibility: visible;
			}
		}
		a:not(.btn) {
			color: var(--header-link-color);
			font-weight: var(--header-link-fw);
			&:hover {
				color: var(--primary-color);
			}
		}
		>li {
			padding: 20px 15px;
			&:first-child {
				padding-left: 0;
			}
			>a {
				&:hover {
					font-weight: var(--header-link-fw);
				}
			}
			mega-dropdown {
				>a {
					position: relative;
					font-size: var(--header-link-fs);
					&:before {
						content: "";
						@include ab(n,0,-0.5px,n);
						width: 100%;
						height: 1px;
						background: var(--primary-color);
						transition: transform 0.5s;
						transform: scaleX(0);
						transform-origin: right;
					}
				}
			}
			&:hover {
				mega-dropdown {
					>a:not(.btn) {
						color: var(--primary-color);
						&:before {
							transform: scaleX(1);
							transform-origin: left;
						}
					}
				}
				>.dropdown {
					opacity: 1;
					visibility: visible;
				}
			}
			&.is-editing {
				>.dropdown {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	.dropdown {
		.dropdown {
			top: -1px;
			@include left(100%);
			opacity: 0;
			visibility: hidden;
		}
		li {
			padding: 5px 20px;
			&:hover {
				.dropdown {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	.mega-section {
		left: 0;
		right: 0;
		.mega-list-block {
			flex-wrap: wrap;
			margin: 0 -10px;
			>.mega-block {
				padding: 0 10px;
			}
		}
		.mega-block-inner {
			padding-bottom: 15px;
		}
	}
}
.mega-section {
	.menu-list {
		@media (min-width: 1000px) {
			li {
				padding: 5px 0;
			}
		}
	}
}
.mega-block {
	.menu-title {
		font-size: 17px;
		line-height: 1.5em;
		font-weight: 500;
		margin-bottom: 12px;
	}
	.mega-block-title {
		margin-bottom: 15px;
		font-weight: 500;
		font-size: 17px;
	}
	&.type-menu { 
		.list-item {
			summary {
				align-items: center; 
			}
		}
		details {
			>summary {
				>svg {
					path {
						fill: var(--header-text-color);
					}
				}
			}
			&[open] {
				>summary {
					>a {
						color: var(--primary-color);
					}
					>svg {
						transform: rotate(180deg);
						transition: 0.2s all ease-out;
						path {
							fill: var(--primary-color);
						}
					}
				}
			}
		}
	}
	&.type-product {
		.block-content {
			width: 100%;
			padding: 30px;
		}
		div.product-title {
			font-size: 20px;
			line-height: 1.2em;
			font-weight: 500;
			a {
				color: inherit;
			}
		}
		.product-weight {
			font-size: 20px;
			line-height: 1.28em;
			font-weight: 500;
		}
		.product-price {
			margin: 20px 0;
			font-size: 25px;
			font-weight: 600;
			line-height: 1.15em;
			color: inherit;
		}
		price-item {
			color: inherit;
		}
		.price-compare {
			font-size: 20px;
			font-weight: 400;
			position: relative;
			top: 2px;
		}
		form .btn {
			padding-top: 7px;
			padding-bottom: 7px;
			height: 36px;
			display: inline-flex;
			align-items: center;
			svg {
				display: none;
			}
			span {
				color: inherit; 
			}
		}
	}
	&.type-banner_overlay {
		.block-content {
			padding: 30px;
		}
		.text {
			font-weight: 500;
		}
		.button-group {
			a {
				height: 36px;
				font-size: 16px;
				padding-top: 7px;
				padding-bottom: 7px;
			}
		}
	}
	&.type-promotion {
		.promo-block {
			padding: 30px;
		}
		.promo-content-wrapper {
			flex-wrap: nowrap;
		}
		.promo-heading {
			margin-bottom: 20px;
			p {
				line-height: 1.2em;
				margin: 0;
			}
		}
		.promo-icon {
			@include margin-end(20px);
			svg {
				width: 60px;
				height: 60px;
			}
		}
		.promo-discount {
			font-size: 50px;
			line-height: 1.2em;
		}
		.promo-content {
			margin-bottom: 20px;
			.text-3 {
				font-size: 14px;
				line-height: 1.45em;
				margin: 0;
			}
		}
		.button-group {
			.btn {
				padding-top: 7px;
				padding-bottom: 7px;
				font-size: 16px;
			}
		}
	}
}
.mega-tab-content-list {
	.mega-tab-list-items {
		padding: 0;
		.mega-tab-content {
			padding: 40px;
		}
	}
}
.mega-tab-heading-list {
	flex: 0 0 18%;
	max-width: 100%;
	padding: 35px 0; 
	position: relative;
	z-index: 22;
	@include border-inline-end (1px solid var(--miscellaneous-color));
	li {
		cursor: pointer;
		padding: 5px 0;
	}
	.highlight h5 {
		text-decoration: underline;
		text-underline-offset: 1px;
	}
	h5 {
		line-height: 1.5em;
		font-size: 15px;
		font-weight: 500;
	}
}
.mega-tab-section {
	padding: 0;
}
// end navigation

// vertical menu
.vertical-menu-link {
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 140px;
	cursor: pointer;
	height: 40px;
	border: 1px solid var(--miscellaneous-color);
	@extend %radius-5px;
	&:before {
		content: "";
		@include fixed(0,0,0,0);
		background: rgba(0,0,0,0.6);
		z-index: -1;
		opacity: 0;
		visibility: hidden;
		transition-delay: 0.2s;
		@extend %transition;
	}
	span {
		display: flex;
		font-weight: 500;
		color: var(--header-text-color);
		@include padding-end(13px);
	}
	.icon-on-show, .icon-on-close {
		margin-top: -1px;
	}
	svg {
		width: 14px;
		height: 14px;
		path {
			stroke: var(--header-link-color);
		}
	}
	.icon-on-close {
		display: none;
	}
}

.vertical-menu {
	>section {
		@include ab(auto,0,n,n);
		width: 100%;
		border: 0;
		padding-top: 25px;
		background: none;
	}
	.mega-container {
		border-top: 1px solid var(--miscellaneous-color);
		background: var(--header-dropdown-bg-color);
	}
	&[open] {
		.vertical-menu-link {
			span {
				color: var(--primary-color);
			}
			.icon-on-show, .icon-on-close {
				svg {
					path {
						stroke: var(--primary-color);
					}
				}
			}
		}
		>section {
			opacity: 1;
			visibility: visible;
		}
	}
	a:not(.btn) {
		color: var(--header-dropdown-text-color);
		font-weight: var(--header-dropdown-text-fw);
		&:hover {
			color: var(--primary-color);
		}
	}
	.vertical-menu-list {
		width: 100%;
		max-width: var(--container);
		margin: 0 auto;
		padding: 33px 0;
		>li {
			padding: 7px 0;
		}
	}
}
.header-vertical-menu {
	@include margin-end(17px);
	>details[open] {
		.icon-on-close {
			display: flex;
		}
		.icon-on-show {
			display: none;
		}
		.vertical-menu-link {
			&:before {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}
// end vertical menu

// search-result
.form-container {
	&.show {
		form {
			@include border-radius(5px,5px,0,0);
		}
		.search-result-container {
			opacity: 1;
			visibility: visible;
			z-index: 111;
		}
	}
}
.search-result-container {
	background: var(--background-color);
	width: 100%;
	z-index: 3;
	border: 1px solid var(--miscellaneous-color);
	border-top: 0;
	@include border-radius(0,0,5px,5px);
	opacity: 0;
	visibility: hidden;
	z-index: -10000;
	.no-result {
		p {
			margin: 0;
		}
	}
}
.search-result-list {
	max-height: 505px;
	overflow-y: auto;
	li {
		padding: 15px 22px;
		border-bottom: 1px solid var(--miscellaneous-color);
		@extend %transition;
		>a {
			align-items: center;
		}
		&:hover {
			background: rgba(0,0,0,0.03);
		}
	}
	.item-image-preview {
		@include margin-end(15px);
	}
	.item-detail {
		.item-title {
			font-size: 13px;
			line-height: 1.5em;
			font-weight: 400;
			@include text-left;
			color: var(--text-color);
		}
		p {
			margin: 0 0 5px;
		}
		.product-price {
			font-size: 14px;
			line-height: 1.2em;
		}
	}
}
#resultContainer {
	overflow: hidden;
	@include border-radius(0,0,5px,5px);
	.view-all-result {
		background: var(--primary-color);
		a {
			display: inline-block;
			padding: 10px 35px;
			color: #fff;
			text-underline-offset: 1px;
		}
	}
} 
.open-mini-search {
	overflow-y: hidden;
}
.layout-boxed {
	.search-dropdown {		
		form {
			max-width: calc(var(--container) - 30px);
			margin: 0 auto;
			border-radius: var(--border-radius);
		}
		#resultContainer{
			max-width: calc(var(--container) - 30px);
			margin: 0 auto;
		}
	}
}
.layout-wide {
	.search-dropdown {		
		.form-search {
			margin: 0 50px;
		} 
		.search-result-container {
			padding: 0 50px;
			border-left: 1px solid var(--miscellaneous-color);
			border-right: 1px solid var(--miscellaneous-color);
		}
	}
}
.mini-search[open] {
	.search-dropdown {
		&:before {
			content: "";
			@include ab(100%,0,0,0);
			background: rgba(0,0,0,0.6);
			height: 100vh;
			pointer-events: none;
		}
	}
}
.search-dropdown {
	width: 100%;
	top: 100%;
	bottom: 0;
	background: var(--background-color);
	z-index: 33;
	height: 70px;
	padding-top: 14px;
	border-bottom: 1px solid var(--miscellaneous-color);
	.btn-popup-close {
		background: var(--background-color);
		@include padding-end(0);
		svg {
			width: 18px;
			height: 18px;
		}
	}
	.form-container {
		display: block;
	}
	input {	
		@include border-radius(var(--border-radius),0,0,var(--border-radius));
		background: var(--header-form-bg-color);
	}
	.btn-search {
		@include border-radius(0,var(--border-radius),var(--border-radius),0);
		background: var(--header-form-bg-color);
		padding-left: 15px;
		padding-right: 15px;
		display: flex;
		align-items: center;
		svg path {
			stroke: var(--primary-color);
		}	
	}
	.form-search {
		display: flex;
		padding: 0 15px;
	}
	.search-result-list {
		.no-result {
			width: 100%;
			border: 0;
		}
		a {
			margin: 0;
		}
		.item-image-preview {
			display: block;
		}
	}
	#resultContainer {
		padding-bottom: 15px;
	}
	.view-all-result {
		border-radius: var(--border-radius);
	}
	.search-result-container {
		position: relative;
		border: 0;
		top: 0;
	}
}
@media (min-width: 750px) {
	.search-result-list {
		.item-detail {
			.item-title {
				font-size: 15px;
			}
			.product-price {
				font-size: 16px;
			}
		}
	}
	.search-dropdown {
		.form-search {
			padding: 0;
		}
		.search-result-list {
			display: flex;
			flex-wrap: wrap;
			li:not(.no-result) {
				flex: 0 0 50%;
				max-width: 50%;
			}
		}
	}
}
@media (min-width: 1000px) {
	.search-dropdown {
		.search-result-list {
			li:not(.no-result) {
				flex: 0 0 33.333%;
				max-width: 33.333%;
			}
		}
		#resultContainer .view-all-result {
			background: none;
			a {
				color: var(--primary-color);
			}
		}
	}
}
// end search-result